import React, {useEffect, useState} from 'react';
import {urlPrefix} from "./settings";
import {
    ApplyCreateCloseButtons,
    daysToSeconds,
    FormGroup,
    makeSubmitHandler,
    randomReadableString
} from "./formHelpers";

function AddRowButton({onClick}) {
    return (
        <button type="button" className={"btn btn-sm btn-light"} onClick={(e) => {
            e.preventDefault()
            if (onClick == null) return
            onClick()
        }}>
            <span className={"fa fa-plus"} />
        </button>
    )
}


function InputRow({handleRemove, state, handleChange, sensorManager}) {
    let [numberUpdating, setNumberUpdating] = useState(0)
    let [lastReading, setLastReading] = useState({time: 0})

    function incrementNumberUpdating(inc=1) {
        setNumberUpdating(n => {
            return n + inc
        });
    }

    function updateChannelList(id, row=state.row) {
        let url = `${urlPrefix}/query/${id}?row=${row}`
        incrementNumberUpdating()
        fetch(url)
            .then(res => {return res.json()})
            .then(res => {
                if (res[0] != null) setLastReading(res[0])
                incrementNumberUpdating(-1)
            })
    }

    function onClickRemove(e) {
        e.preventDefault()
        handleRemove()
    }

    function onClickRefresh(e) {
        e.preventDefault()
        updateChannelList(state.id)
    }

    function onChange(event) {
        event.preventDefault()
        let newState = {...state};
        newState[event.nativeEvent.target.name] = event.nativeEvent.target.value
        handleChange(newState)
        if (event.nativeEvent.target.name === "id") {
            updateChannelList(event.nativeEvent.target.value)
        }
        if (event.nativeEvent.target.name === "row") {
            updateChannelList(state.id, event.nativeEvent.target.value)
        }
    }

    useEffect(() => {
        state.id && updateChannelList(state.id)
    }, [state.id]);

    return ([
        <div key={"remove-button"}>
            <button type="button" className={"btn btn-sm btn-light"} onClick={onClickRemove}>
                <span className={"fa fa-minus"} />
            </button>
        </div>,
        <div key={"sensor"}>
            <select
                   className="form-control form-control-sm mr-2"
                   placeholder="Sensor"
                   name={"id"}
                   value={ state.id }
                   onChange={onChange}>
                <option value=""/>
                { sensorManager.items().map(item => {
                    return <option key={item.id} value={item.id}>{item.name}</option>
                }) }
            </select>
        </div>,
        <div key={"channel"}>
            <select
                   className="form-control form-control-sm mr-2"
                   placeholder="Channel"
                   name={"channel"}
                   value={ state.channel }
                   onChange={onChange}>
                { Object.keys(lastReading).map(item => {
                    return <option key={item} value={item}>{item}</option>
                }) }
            </select>
        </div>,
        <div key={"index"}>
            <input type="number"
                   className="form-control form-control-sm mr-2"
                   placeholder="-1"
                   name={"row"}
                   value={ state.row }
                   onChange={onChange}/>
        </div>,
        <div key={"placeholder"}/>,
        <div key={"variable"}>
            <input type="text"
                   className="form-control form-control-sm"
                   placeholder="Variable name (can be used in Expression)"
                   name={"variable"}
                   value={ state.variable }
                   onChange={onChange}/>
        </div>,
        <div key={"current-value"}>
            <div className="form-control form-control-sm bg-light text-truncate text-nowrap text-monospace">
                { lastReading[state.channel] }
            </div>
        </div>,
        <div key={"update-button"} className={"mb-2"}>
            <button type="button" className={"btn btn-sm btn-light w-100"} onClick={onClickRefresh}>
                {numberUpdating > 0 ?
                    <i className={"spinner-border spinner-border-sm"}/> :
                    <i className={"fa fa-sync"}/>}
            </button>
        </div>
    ])
}


function ActionIDsFrom({actions, setActions, actionManager}) {
    return (
        <div>
            <ul className={"list-group"}>
                {actions.map((action, idx) => {
                    return (
                        <div className={"list-group-item p-0"} key={idx}>
                            <div className={"input-group"}>
                                <div className="input-group-prepend">
                                    <button
                                        className="btn btn-light"
                                        type="button"
                                        onClick={() => setActions(
                                            actions.filter((item, i) => idx !== i)
                                        )}>
                                        <i className={"fas fa-minus"}/>
                                    </button>
                                </div>
                                <select
                                    className={"border-0 custom-select"}
                                    value={action}
                                    onChange={event => {
                                        let newActions = [...actions]
                                        newActions[idx] = event.nativeEvent.target.value
                                        setActions(newActions)
                                    }}>
                                    {actionManager.items().map((item, idxOption) => {
                                        return <option value={item.id} key={idxOption}>
                                            {item.name}
                                        </option>
                                    })}
                                </select>
                            </div>
                        </div>
                    )
                })}
            </ul>
            <div
                className={"btn btn-light mt-1"}
                onClick={() => {
                    setActions([
                        ...actions,
                        actionManager.items().length > 0  ? actionManager.items()[0].id : ""
                    ])
                }}>
                <i className={"fa fa-plus"}/>
            </div>
        </div>
    )
}


const emptyInputRow = {channel: "time", row: -1, id:"", variable: "A"}
const initialState = {
    name: "New Trigger",
    retain_for: 90 * daysToSeconds,
    expression: "",
    variables: [emptyInputRow],
    action_ids: [],
    message: "",
    expressionMessage: "",
    expressionError: false,
}


function InputRows({variables, setVariables, sensorManager}) {

    function newInputRow() {
        if (variables.length === 0) return emptyInputRow
        // make sure to deep copy the variable object
        let newRow = {...variables.slice(-1)[0]}
        newRow.variable = randomReadableString(3)
        setVariables([...variables, newRow])
    }

    return ([
        ...variables.map((variable, idx) => {
            return <InputRow
                sensorManager={sensorManager}
                key={idx}
                state={variable}
                handleRemove={() => {
                    setVariables(
                        variables.filter((_, i) => {
                            return i !== idx
                        })
                    )
                }}
                handleChange={(newState) => {
                    let newVariables = [...variables]
                    newVariables[idx] = newState
                    setVariables(newVariables)
                }}
            />
        }),
        <AddRowButton key={"add-button"} onClick={() => {newInputRow()}}/>
    ])
}


export function TriggerFrom({editMode=false, state=initialState, setState, handleClose=()=>{},
                               setActive=()=>{}, actionManager, sensorManager}) {

    // evaluate expression when form is shown, or variables or expression changes
    useEffect(() => {
        if (state.expression === "") return
        evaluateExpression(state.expression, state.variables)
    }, [state.expression, state.variables]);


    const handleSubmit = makeSubmitHandler(
        editMode, handleClose, state, setState, initialState, setActive, "triggers")


    function evaluateExpression(expression, variables) {
        let data = {
            expression: expression,
            variables: variables,
        }

        fetch(urlPrefix + "/evaluate", {
            method: "post",
            body: JSON.stringify(data)
        })
            .then(res => {return res.json()})
            .then(res => {
                setState({...state, expressionMessage: res.message, expressionError: res.error})
            })
    }

    const stateProps = {state: state, setState: setState}

    return (
        <form onSubmit={handleSubmit}>
            <div className={"row"}>
                <div className={"col-md-6"}>
                    <FormGroup
                        label={"Name:"}
                        name={"name"}
                        {...stateProps}/>
                </div>
                <div className={"col-md-6"}>
                    <FormGroup
                        label={"Retain history for:"}
                        type={"number"}
                        name={"retain_for"}
                        suffix={"days"}
                        factor={daysToSeconds}
                        {...stateProps}/>
                </div>
            </div>

            <hr/>

            <h4>Variables</h4>
            <div className={"variable-input-grid"}>
                <div/>
                <div>Sensor:</div>
                <div>Channel:</div>
                <div>Index:</div>
                <InputRows
                    sensorManager={sensorManager}
                    variables={state.variables}
                    setVariables={(variables => {
                        setState({...state, variables: variables})
                    })}/>
            </div>


            <hr/>
            <h4>Expression</h4>

            <FormGroup
                type="text"
                className="form-control text-monospace mb-1"
                placeholder={"A == 3 or \"substing\" in long_string"}
                name={"expression"}
                {...stateProps}/>

            {state.expression === "" ? null :
                <div className={`alert ${state.expressionError ? "alert-danger" : 
                    [true, false].includes(state.expressionMessage) ? "alert-success" : "alert-warning"}`}>
                    {"expressionMessage" in state && state.expressionMessage.toString()}
                </div>
            }

            <h4>Action</h4>
            <ActionIDsFrom
                actionManager={actionManager}
                actions={state.action_ids}
                setActions={actions => {
                    setState({
                        ...state,
                        action_ids: actions
                    })
                }}/>

            <FormGroup
                label={"Message:"}
                name={"message"}
                {...stateProps}/>

            <ApplyCreateCloseButtons label={"Trigger"} handleClose={handleClose} editMode={editMode}/>
        </form>
    )
}


