import React, {useEffect} from "react";
import Chart from "chart.js";
import 'chartjs-plugin-zoom'

// React.memo should prevent the rerendering except, if the props change.
export const LineChart = React.memo(function LineChart(props) {
    function makeLineChartData(data) {
        return {
            labels: data.map((x, i) => i),
            datasets: [{
                label: 'My First dataset',
                borderColor: "#fa9121",
                backgroundColor: "#fa912111",
                fill: true,
                data: data,
                hidden: true,
            }, {
                label: 'My Second dataset',
                borderColor: "#21af61",
                backgroundColor: "#21af61",
                fill: false,
                data: data.map(x => x * 1.2),
            }]
        }
    }

    let options = {
        responsive: true,
        hoverMode: 'index',
        stacked: false,
        title: {
            display: true,
            text: props.title || ""
        },
        scales: {
            yAxes: [{
                type: 'linear', // only linear but allow scale type registration. This allows extensions to exist solely for log scale for instance
                display: true,
                position: 'left',
                id: 'y-axis-1',
            }, {
                type: 'linear', // only linear but allow scale type registration. This allows extensions to exist solely for log scale for instance
                display: false,
                position: 'right',
                id: 'y-axis-2',

                // grid line settings
                gridLines: {
                    drawOnChartArea: false, // only want the grid lines for one axis to show up
                },
            }],
            xAxes: [{
                type: 'time',
            }, ],
        },
        plugins: {
            zoom: {
                pan: {
                    enabled: false,
                },
                zoom: {
                    enabled: true,
                    drag: true,
                    mode: 'x',
                    speed: 1,
                    threshold: 0.1,
                }
            }
        },
    }

    let chartExists = false
    useEffect(() => {
        if (!chartExists) {
            makeChart()
            chartExists = true
        }
    }, [props.data]);

    function makeChart() {
        let chart = Chart.Line(chartRef.current, {
            data: makeLineChartData(props.data),
            options: options
        })
        chartRef.current.ondblclick = () => {
            chart.resetZoom()
        }
    }

    let chartRef = React.createRef();

    return <canvas ref={chartRef}/>
})