import {urlPrefix} from "./settings";

// the state manager itself is stateless. All its state is stored in the "globalState" of the root ract component
export class stateManager {
    constructor({eventManager,
                 itemLabel="Item",
                 eventHandlerItemPrefix = "item",
                 fetchUrlPath="/items"}) {

        // Is this state manager for a Sensor, Trigger or otherwise?
        this.itemLabel = itemLabel
        this.stateKey = itemLabel + "State"
        this.eventHandlerItemPrefix = eventHandlerItemPrefix
        this.fetchUrlPath = fetchUrlPath

        // dummy state
        this.globalState = {}
        this.setGlobalState = ({}) => {}

        // listen to incoming events. The eventManager will call the "eventHandler" function
        this.eventManager = eventManager
        eventManager.subscribe(this)
    }

    initializeGlobalState(globalState, setGlobalState) {
        this.globalState = globalState
        this.setGlobalState = setGlobalState
    }

    eventHandler(event) {
        if (event.message === this.eventHandlerItemPrefix + " deleted") {
            // remove item from list
            let id = event.data.id
            let newIdx = 0
            let filtered = this.items().filter((item, index) => {
                if (item.id === id) {
                    newIdx = index
                }
                return item.id !== id;
            });

            // delete item
            this.setItems(filtered)

            // focus on new item
            if (this.items().length > 0) {
                newIdx = this.items().length <= newIdx ? newIdx - 1 : newIdx
                this.setState( {
                    active: this.items()[newIdx].id
                } )
            }

        } if (event.message === this.eventHandlerItemPrefix + " added") {
            this.setItems([
                ...this.items(),
                event.data
            ] )

        } if (event.message === this.eventHandlerItemPrefix + " edited") {
            // update the relevant item
            this.updateItem(event.data.id)

        } if (event.message === this.eventHandlerItemPrefix + " updated") {
            let id = event.data.id
            // set last update time
            this.setActiveState({lastUpdateEvent: Date.now() / 1000}, id)
            if (this.state().active === id) {
                // update item if currently visible
                this.updateItem(id)
            } else {
                // remove outdated reading if not visible
                let items = this.items()
                let idx = items.findIndex(obj => obj.id === id)
                if (items[idx].last_reading) {
                    items[idx].last_reading = null
                }
                event.data.reading != null && (items[idx].last_reading = event.data.reading)
                this.setItems(items)
            }
        }
    }

    state() {
        // initialize other state
        if (this.globalState[this.stateKey] == null) {
            this.globalState[this.stateKey] = {}
        }
        return this.globalState[this.stateKey]
    }

    setState(newState) {
        let state = this.state()
        let newGlobalState = {}
        newGlobalState[this.stateKey] = {...state, ...newState}
        this.setGlobalState(newGlobalState)
    }

    activeState(id=this.state().active) {
        // state of currently active item
        if (id == null) return {}
        // initialize
        if (this.globalState[id] == null) {
            this.globalState[id] = {}
            return {}
        }
        return this.globalState[id]
    }

    setActiveState(newActiveState, id=this.state().active) {
        if (id == null) return

        let newState = {}
        newState[id] = {...this.activeState(id), ...newActiveState}
        this.setGlobalState(newState)
    }

    items() {
        // initialize list of sensors or triggers, ect
        if (this.globalState[this.itemLabel] == null) {
            this.globalState[this.itemLabel] = []
            this.updateItems()
            return []
        }
        return this.globalState[this.itemLabel]
    }

    setItems(items) {
        let newState = {}
        newState[this.itemLabel] = items
        this.setGlobalState(newState)
    }

    item() {
        let item = this.items().find(obj => {
            return obj.id === this.state().active
        })
        // item not found
        if (item == null) {
            item = {}
        }
        return item
    }

    setItem(item) {
        let items = this.items()
        let idx = items.findIndex(obj => obj.id === item.id)
        items[idx] = item
        this.setItems(items)
    }

    updateItem(id) {
        if (id == null) return
        let url = urlPrefix + `${this.fetchUrlPath}/${id}`
        fetch(url)
            .then(res => {return res.json()})
            .then(data => {
                this.setItem(data)
            })
    }

    updateItems() {
        let url = urlPrefix + this.fetchUrlPath
        fetch(url)
            .then(res => {return res.json()})
            .then(res => {
                this.setItems(res)
                if (this.state().active === "" && this.items().length > 0) this.setState({
                    active: "id" in this.items()[0] ? this.items()[0].id : "form"
                })
            })
            .catch(() => {
                this.state().active === "" && this.setState({
                    active: "form"
                })
            })
    }

    refreshItem(id) {
        if (id == null) return
        let url = urlPrefix + `${this.fetchUrlPath}/${id}/update`
        return fetch(url)
            .then(res => {return res.json()})
            .then(data => {
                this.setItem({
                    ...this.item(),
                    last_reading: data
                })
            })
    }

    deleteItem(id) {
        if (id == null) return
        let url = urlPrefix + `${this.fetchUrlPath}/${id}`;
        return fetch(url, {
            method: 'DELETE',
            data: {}
        });
    }

    addItem(data) {
        let url = urlPrefix + this.fetchUrlPath;
        console.log(JSON.stringify(data))
        return fetch(url, {
            method: 'POST',
            body: JSON.stringify(data)
        });
    }

}