import { urlPrefix } from './settings.js';


export class eventManager {
    constructor() {
        this.subscriptions = []
        this.listen()
    }


    subscribe(obj) {
        this.subscriptions.push(obj)
    }


    unsubscribe(obj) {
        let idx = this.subscriptions.indexOf(obj)
        delete this.subscriptions[idx]
    }


    handleEvent(event) {
        this.subscriptions.forEach(obj => {
            try {
                obj.eventHandler(event)
            }
            catch (e) {
                console.log(e)
            }
        })
    }


    listen () {
        let that = this;
        let source = new EventSource(urlPrefix + "/events");
        source.onmessage = function (e) {
            try {
                let data = JSON.parse(e.data)
                that.handleEvent(data)
            } catch (error) {}
        }
    }

}