import React, {useState} from 'react';
import {urlPrefix} from "./settings";
import {
    ApplyCreateCloseButtons,
    daysToSeconds,
    FormGroup,
    FormGroupSelect,
    makeSubmitHandler,
    onChangeHandler
} from "./formHelpers";
import {stateManager} from "./state";

const initialState = {
    "type": "HTML",
    "interval": 5 * 60,
    "name": "",
    "url": "",
    "retain_for": 90 * daysToSeconds};

export function SensorFrom({editMode=false, state=initialState, setState, handleClose=()=>{},
                               setActive=(active)=>{}, ...props}) {

    const handleSubmit = makeSubmitHandler(editMode, handleClose, state, setState, initialState, setActive, "sensors")

    const stateProps = {state: state, setState: setState}

    return (
        <form onSubmit={handleSubmit} className={"row"}>
            <div className={"col-lg-4"} style={{ borderRight: "1px solid #dee2e6" }}>
                <h4>Type</h4>
                <FormGroupSelect
                    label={" "}
                    type={"select"}
                    value={state.type}
                    list={"form-add-sensor-types"}
                    name={"type"}
                    onChange={onChangeHandler(state, setState)}
                    disabled={editMode}
                >
                    <option value={"HTML"}>HTML - Grabs HTTP response from a URL</option>
                    <option value={"CPUPercentage"}>CPUPercentage - Host system CPU usage</option>
                    <option value={"RAMPercentage"}>RAMPercentage - Host system memory usage</option>
                    <option value={"Uptime"}>Uptime - Host system uptime</option>
                </FormGroupSelect>
            </div>

            <div className={"col-lg-4"} style={{ borderRight: "1px solid #dee2e6" }}>
                <h4>Info</h4>
                <FormGroup {...stateProps} label={"Name:"} name={"name"}/>
                <FormGroup
                    {...stateProps}
                    label={"Update interval:"}
                    name={"interval"} type={"number"}
                    suffix={"minutes"}
                    factor={60}
                />
                <FormGroup
                    {...stateProps}
                    label={"Retain for:"}
                    name={"retain_for"}
                    type={"number"}
                    suffix={"days"}
                    factor={daysToSeconds}/>

            </div>

            <div className={"col-lg-4"}>
                <h4>Type Specific Info</h4>
                { state.type === "HTML" ? (
                    <FormGroup label={"Target URL:"} name={"url"} {...stateProps}/>
                ) : "" }
            </div>

            <ApplyCreateCloseButtons handleClose={handleClose} editMode={editMode} label={"Sensor"}/>
        </form>
    )
}


