import React, {useCallback, useEffect, useState} from "react";

export const Modal = ({handleClose, show, children, title}) => {
    function maybeClose(event) {
        if (event.target.classList.contains("modal")) {
            handleClose()
        }
    }

    const escFunction = useCallback((event) => {
        if (event.key === "Escape") {
            handleClose()
        }
    }, [handleClose]);

    useEffect(() => {
        document.addEventListener("keydown", escFunction, false);

        return () => {
            document.removeEventListener("keydown", escFunction, false);
        };
    }, [escFunction]);

    return (
        <div className={`modal fade ${show ? "show" : ""}`}
             tabIndex="-1"
             role="dialog"
             style={{
                 "display": show ? "block" : "none",
                 "backgroundColor": "rgba(1,1,31,0.22)",
                 backdropFilter: "blur(8px)",
                 overflowY: "auto"
             }}
             onClick={(event) => maybeClose(event)}
        >
            <div className="modal-dialog modal-xl" role="document">
                <div className="modal-content shadow">
                    <div className="modal-header bg-light">
                        <h5 className="modal-title">{title}</h5>
                        <button type="button" className="close" onClick={() => {
                            handleClose()
                        }}>
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div className="modal-body">
                        {children}
                    </div>
                </div>
            </div>

        </div>
    )
}


export function ListItemSelect(props) {
    return (
        <li
            className={`list-group-item list-group-item-transparent list-group-item-action 
            ${props.active ? "active" : ""} ` + props.className}
            style={{cursor: "default"}}
            onClick={props.onClick}
        >
            <span style={{width: "48px"}}>{props.iconLeft}</span>
            {props.children}
            <span className={"float-right"}>{props.iconRight}</span>
        </li>
    )
}


export function ListItemCollapse(props) {
    let [show, setShow] = useState(false);

    return (
        [
            <li className={"list-group-item list-group-item-action"}
                onClick={() => setShow(!show)}
                style={{cursor: "pointer", borderBottom: "none"}}
                key={"1"}
            >
                <i className={"fas fa-chevron-right fa-sm mr-2"} style={show ? {transform: "rotate(90deg)"} : {}}/>
                <span className="text-monospace">
                    {props.label}
                </span>
                <span className="float-right font-weight-light">
                    {props.value}
                </span>
            </li>,
            <li className={`list-group-item custom-collapse ${show ? "show" : ""} py-0`} key={"2"}>
                <div className={"py-3"}>
                    {show ? props.children : null}
                </div>
            </li>
        ]
    )
}