import React from "react"
import {listItemsLastReading} from "./DetailView";
import {LineChart} from "./Chart";
import {urlPrefix} from "./settings";


export function Sensor({stateManager}) {

    let activeDetailView = stateManager.state().activeDetailView || 0
    const setActiveDetailView = active => {
        stateManager.setState({activeDetailView: active})
    }

    function getChartData() {
        let url = `${urlPrefix}/query/${stateManager.item().id}?row=elapsed`
        fetch(url)
            .then(res => {return res.json()})
            .then(res => {
                console.log(res)
                stateManager.setActiveState({chartData: res})
            })
    }

    return (
        <div className={"mt-3"}>
            { stateManager.item().type === "HTML" && (
                <button
                    className={"btn btn-sm btn-outline-primary mb-2"}
                    onClick={() => {
                        let wnd = window.open(stateManager.item().url)
                        wnd.document.write(stateManager.item().last_reading.content)
                    }}>
                    <span className={"fa fa-external-link-alt mr-2"}/>
                    Open HTML content of last reading in new window
                </button>
            )}
            <div className="btn-group mb-2 w-100">
                <button
                    type="button"
                    className={`btn btn-light ${activeDetailView === 0 ? "active" : ""}`}
                    onClick={() => {setActiveDetailView(0)}}>
                    <span className={"fas fa-database mr-2"}/>
                    Raw Data
                </button>
                <button
                    type="button"
                    className={`btn btn-light ${activeDetailView === 1 ? "active" : ""}`}
                    onClick={() => {setActiveDetailView(1)}}>
                    <span className={"fas fa-chart-area mr-2"}/>
                    History
                </button>
            </div>

            {activeDetailView === 0 &&
            <div>
                <ul className={"list-group"}>
                    {listItemsLastReading(stateManager.item().last_reading)}
                </ul>
                <span className={"text-muted font-weight-normal"}>data from last reading</span>
            </div>
            }

            {activeDetailView === 1 &&
                <div>
                    Work in progress
                    {//<LineChart data={stateManager.activeState().chartData || [0, 2, 3, 1, 4, 2, 4, 2, 1]}/>
                    }
                </div>
            }

        </div>
    )
}