import React, {useEffect, useState} from "react";
import {ListItemSelect} from "./Basics";


function BadgeMessage({stateManager, item}) {
    function badge(child, color="light") {
        return (
            <div className={"badge font-weight-normal badge-" + color}>
                {child}
                { stateManager.activeState(item.id).editMode && <i className={"fas fa-edit ml-2"}/> }
            </div>
        )
    }

    if (stateManager.eventHandlerItemPrefix === "trigger") {
        // no data for last reading? early return
        if (item.last_reading == null) return null

        // broken?
        if (item.last_reading.state == null) {
            return badge("broken", "warning")
        }

        return item.last_reading.state ? badge("triggering", "primary") : badge("not triggering")
    }

    return badge(item.type)
}


function Item({stateManager, item}) {
    let [flash, setFlash] = useState("initial")

    useEffect(() => {
        // do not do anything on initial render
        if (flash === "initial") {
            setFlash("")
            return;
        }
        setFlash("flash")
        setTimeout(() => setFlash(""), 1000)
    }, [stateManager.activeState(item.id).lastUpdateEvent])

    return (
        <ListItemSelect
            className={flash}
            onClick={() => stateManager.setState({ active: item.id })}
            active={stateManager.state().active === item.id}
            iconRight={<BadgeMessage stateManager={stateManager} item={item}/>}>
            { item.name }
        </ListItemSelect>
    )
}

export function ListView({stateManager}) {
    useEffect(() => {
        if (stateManager.state().active == null && stateManager.items()[0] != null) {
            stateManager.setState({
                active:  stateManager.items()[0].id
            })
        }
    }, [stateManager.state().active, stateManager.items()])

    function ItemList(items) {
        if (items == null) return null

        return items.map(item => <Item stateManager={stateManager} item={item} key={item.id}/>);
    }

    return [
        <li className={`list-group-item list-group-item-transparent list-group-item-action 
        ${stateManager.state().active === "form" ? "active" : ""}`}
            key={"button"}
            style={{cursor: "default"}}
            onClick={() => stateManager.setState({ active: "form" })}
        >
            <h3 className={"mb-1"}>
                New {stateManager.itemLabel}
                <span className={"float-right"}>
                    <i className={"fa fa-xs fa-plus"}/>
                </span>
            </h3>
        </li>,

        <input className={"input-group-sm list-group-item list-group-item-transparent w-100 icon-placeholder py-2"}
               placeholder="&#xf002; Filter"
               value={stateManager.state().filter}
               key={"Filter"}
               onChange={event => stateManager.setState({filter: event.target.value})}
        />,

        ItemList( stateManager.items().filter(item => {
            let filter = (stateManager.state().filter || "").toLowerCase()
            return Object.keys(item).some(key => {
                try {
                    return item[key].toLowerCase().includes(filter)
                } catch (e) {
                    return false
                }
            })
        }))
    ]

}



