import React, {useState} from 'react';
import {urlPrefix} from "./settings";
import {
    ApplyCreateCloseButtons,
    daysToSeconds,
    FormGroup,
    FormGroupSelect,
    makeSubmitHandler,
    onChangeHandler
} from "./formHelpers";

const initialState = {
    "type": "DebugAction",
    "interval": 5 * 60,
    "name": "",
    "url": "",
    "retain_for": 90 * daysToSeconds};


export function ActionForm({editMode=false, state=initialState, setState, handleClose=()=>{},
                               setActive=(active)=>{}, ...props}) {

    const handleSubmit = makeSubmitHandler(editMode, handleClose, state, setState, initialState, setActive, "actions")

    function testAction() {
        let message = `${state.name}: Test message :)`;
        fetch(urlPrefix + "/actions/" + state.id + "/update", {
            method: "post",
            body: JSON.stringify({
                message: message,
                force_send: true
            })
        })
    }

    const stateProps = {state: state, setState: setState}

    function pushoverInputs() {
        return [
            <FormGroup label={"Api token:"} name={"api_token"} key={"api_token"} {...stateProps}/>,
            <FormGroup label={"User key:"} name={"user_key"} key={"user_key"} {...stateProps}/>,
            <FormGroup label={"Device:"} name={"device"} key={"device"} {...stateProps}/>,
        ]
    }

    return (
        <form onSubmit={handleSubmit} className={"row"}>
            <div className={"col-lg-4"} style={{ borderRight: "1px solid #dee2e6" }}>
                <h4>Type</h4>
                <FormGroupSelect
                    label={" "}
                    type={"select"}
                    value={state.type}
                    list={"form-add-sensor-types"}
                    name={"type"}
                    onChange={onChangeHandler(state, setState)}
                    disabled={editMode}
                >
                    <option value={"DebugAction"}>DebugAction - Debug print statement</option>
                    <option value={"PushoverAction"}>Pushover - Sends a push notification</option>
                </FormGroupSelect>
            </div>

            <div className={"col-lg-4"} style={{ borderRight: "1px solid #dee2e6" }}>
                <h4>Info</h4>
                <FormGroup
                    label={"Name:"}
                    name={"name"}
                    {...stateProps}/>
                <FormGroup
                    label={"Cooldown:"}
                    name={"cooldown"}
                    type={"number"}
                    suffix={"minutes"}
                    factor={60}
                    {...stateProps}/>
                <FormGroup
                    label={"Retain for:"}
                    name={"retain_for"}
                    type={"number"}
                    suffix={"days"}
                    factor={daysToSeconds}
                    {...stateProps}/>
            </div>

            <div className={"col-lg-4"}>
                <h4>Type Specific Info</h4>
                { state.type === "PushoverAction" ? (
                    pushoverInputs()
                ) : "" }
            </div>

            <ApplyCreateCloseButtons editMode={editMode} handleClose={handleClose} label={"Action"}/>
        </form>
    )
}


