import React, {useEffect, useState} from "react"
import {ListItemCollapse} from "./Basics";
import moment from "moment";

function truncate(str) {
    if (str !== null) {
        str = str.toString()
        return str.length < 40 ? str : `${str.slice(0, 37)}...`
    }
}

function minuteStr(x) {
    return `${Math.round(x / 60 * 10) / 10} minute${x !== 60 ? "s" : ""}`
}

let knownConverters = {
    elapsed: x => {
        return `${Math.round(x * 1000)} ms`
    },
    time: x => {
        return new Date(x * 1000).toLocaleString()
    },
    percentage: x => {
        return `${Math.round(x * 10) / 10} %`
    },
    seconds: x => {
        return `${Math.round(x)} (${Math.round(10 * x / 60 / 60 / 24) / 10} days)`
    },
    url: x => {
        return (
            <a href={decodeURIComponent(x)} target="_blank" rel="noopener noreferrer">
                { decodeURIComponent(x) }
            </a>
        )
    },
    interval: minuteStr,
    cooldown: minuteStr,
    type: x => {
        return <kbd className={"bg-light text-dark"}>
            {x}
        </kbd>
    },
    retain_for: x => {
        return `${Math.round(x / 24 / 60 / 60 * 10) / 10} days`
    },
    expression: x => {
        return <div className={"text-monospace"}>{x}</div>
    },
    broken: x => {
        return !x ? <span className={"fas fa-check text-success"}/> : <span className={"fas fa-times text-danger"}/>
    },
    last_notify: x => {
        return x < 0 ?
            "never" :
            `${new Date(x * 1000).toLocaleString()} (${moment(x * 1000).fromNow()})`
    },
    queued_messages: x => {
        return x.map((item, idx) => {
            return <div key={idx}>
                {item}
            </div>
        })
    },
    action_ids: x => {
        return x.map((item, idx) => {
            return <kbd key={idx}>
                {item}
            </kbd>
        })
    },
}
let converters = new Proxy(knownConverters, {
    get: (target, name) =>
        name in target ? target[name] : truncate
})


let ignoreList = ["last_reading", "last_update", "kwargs", "enabled", "id", "name"]

let knownLabelConversions = {
    "url": "Target URL",
    "retain_for": "Retain readings for",
    "last_notify": "Last notified",
    "queued_messages": "Queued messages",
    "api_token": "Api token",
    "user_key": "User key",
    "action_ids": "Actions",
}
let labelConversion = new Proxy(knownLabelConversions, {
    get: (target, name) =>
        name in target ? target[name] : <span className={"text-capitalize"}>{name}</span>
})


export function listItemsLastReading(lastReading) {
    if (lastReading == null) return (
        <i className={"spinner-grow spinner-grow-lg text-secondary mx-auto my-4"}/>
    )

    let listItems = Object.keys(lastReading).map(function (key) {
        return <ListItemCollapse
            key={key}
            label={key}
            value={converters[key](lastReading[key])}
        >
            {lastReading[key]}
        </ListItemCollapse>
    });
    return (
        listItems
    )
}


function AttributeRow({label, children}) {
    return (
        <li className={"list-group-item px-0"}>
            <div className={"row"}>
                <div className={"col-3 h5 text-secondary m-0"}>
                    {label}
                </div>
                <div className={"col-9"}>
                    {children}
                </div>
            </div>
        </li>
    )
}


function Button({showSpinner, className, ...props}) {
    return (
        <button
            className={"btn btn-light mr-2 " + className}
            {...props}>
            {showSpinner ?
                <i className={"spinner-border spinner-border-sm"}/> :
                props.children}
        </button>
    )
}

function YouSureButton({children, onClick, sureClass="border-primary", ...props}) {
    let [showDialog, setShowDialog] = useState(false)

    return (
        <Button
            onClick={(e) => {
                showDialog && onClick(e)
                !showDialog && setTimeout(() => {setShowDialog(false)}, 3000)
                setShowDialog(!showDialog)
            }}
            className={showDialog ? sureClass : ""}
            {...props}>
            {children}
        </Button>
    )

}


function Buttons({stateManager}) {
    let [updating, setUpdating] = useState(false)
    let [copying, setCopying] = useState(false)
    let id = stateManager.state().active

    function refresh() {
        if (updating) {return}
        setUpdating(true)
        stateManager.refreshItem(id)
            .then(() => {setUpdating(false)})
            .catch(() => {setUpdating(false)})
    }

    function copy() {
        let {last_reading, last_update, id, ...item} = stateManager.item()
        item.name += " (copy)"
        setCopying(true)
        stateManager.addItem(item)
            .then(() => {setCopying(false)})
            .catch(() => {setCopying(false)})
    }

    return (
        <div className={"header-buttons float-right my-auto"}>
            <Button showSpinner={updating} onClick={() => refresh()} disabled={stateManager.activeState().editMode}>
                <i className={"fa fa-sync"}/>
            </Button>

            <YouSureButton showSpinner={copying} onClick={() => copy()} disabled={stateManager.activeState().editMode}>
                <i className={"fa fa-copy"}/>
            </YouSureButton>

            <Button
                onClick={() => stateManager.setActiveState({
                    editMode: !stateManager.activeState().editMode
                })}>
                <i className={"fa fa-edit"}/>
            </Button>

            <YouSureButton
                sureClass={"border-danger"}
                onClick={() => stateManager.deleteItem(id)}>
                <i className={"fa fa-trash text-danger"}/>
            </YouSureButton>
        </div>
    )

}

export function DetailView({stateManager, FormView, children, ...props}) {
    let id = stateManager.state().active

    useEffect(() => {
        stateManager.item().last_reading == null && stateManager.updateItem(id)
    }, [id])

    function attributeRows(attributes) {
        let statefulConverters = {
            variables: x => {
                return x.map((variable, idx) => {
                    let sensorName = variable.id
                    try {
                        sensorName = props.sensorManager.items().find(s => s.id === variable.id).name
                    } catch (e) {}

                    return <div className={"text-monospace text-large"} key={idx}>
                        <code className={""}>{variable.variable}</code> = <kbd>
                            {sensorName}
                        </kbd>.
                        <kbd>{variable.channel}</kbd>
                        [<kbd>{variable.row}</kbd>]
                    </div>
                })
            }
        }

        return (
            <ul className="list-group list-group-flush">
                {Object.entries(attributes).map(entry => {
                    let key = entry[0]
                    let value = entry[1]
                    return (value != null && value !== "" && !(ignoreList.includes(key))) && (
                        <AttributeRow label={labelConversion[key]} key={key}>
                            { key in statefulConverters ? statefulConverters[key](value) : converters[key](value)}
                        </AttributeRow>
                    )
                })}
            </ul>
        )
    }

    function renderDetail() {
        return (
            <div className={"main px-3"} key={"detail"}>
                { attributeRows(stateManager.item()) }
                { children }
            </div>
        )
    }

    function setFormState(state) {
        stateManager.setActiveState({
            formState: {...stateManager.activeState().formState, ...state}
        })
    }

    function renderForm() {
        return (
            <div className={"main px-3"}>
                <FormView
                    key={"form"}
                    state={{...stateManager.item(), ...stateManager.activeState().formState}}
                    setState={setFormState}
                    editMode={true}
                    handleClose={() => {stateManager.setActiveState({editMode: false, formState: {}})}}
                    {...props}
                />
            </div>
        )
    }

    if (stateManager.activeState().editMode === true) {
        return ([
            <Buttons stateManager={stateManager} key={"Buttons"}/>,
            renderForm()
        ])
    } else {
        return ([
            <Buttons stateManager={stateManager} key={"ButtonsDetail"}/>,
            renderDetail()
        ])
    }
}